.category {
  padding: 60px 0px 60px 0px;
  @media (max-width: 768px) {
    padding: 40px 0px;
  }
  @media (max-width: 468px) {
    padding: 20px 0px 35px 0px;
  }

  // .category__navigation

  &__navigation {
  }

  // .category__body

  &__body {
    margin: 20px 0px 0px 0px;
  }

  // .category__content

  &__content {
    position: relative;
  }

  // .category__slider

  &__slider {
    margin: -20px -18px;
    padding: 20px 18px;
    width: calc(100% + 36px);
    min-height: 340px;
    &.no-filter {
      min-height: 0px;
    }
  }

  // .category__wrapper

  &__wrapper {
  }

  // .category__slide

  &__slide {
    display: flex;
    height: auto;
    &._hide {
      display: none;
    }
  }

  // .category__item

  &__item {
    display: flex;
    flex-direction: column;
    width: 100%;
    position: relative;
    border-radius: 7px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.3);
    overflow: hidden;
  }

  // .category__bg

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(75, 55, 39, 0) 0%, #4b3727 50.44%);
  }

  // .category__close

  &__close {
    cursor: pointer;
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 10;
    transition: color 0.3s;
    color: rgba(255, 255, 255, 0.5);
    &:hover {
      color: rgba(255, 255, 255, 1);
    }
  }

  // .category__image

  &__image {
    display: flex;
    height: 160px;
    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }

  // .category__header

  &__header {
    display: flex;
    flex-direction: column;
    height: 100%;
    position: relative;
    z-index: 5;
    padding: 6px 20px 23px 20px;
    @media (max-width: 768px) {
      padding: 6px 20px 20px 20px;
    }
  }

  // .category__top

  &__top {
    flex: 1 1 auto;
    margin: 0px 0px 20px 0px;
  }

  // .category__title

  &__title {
    font-weight: 400;
    font-size: 12px;
    line-height: 133%;
    color: #c8c8c8;
    margin: 0px 0px 5px 0px;
  }

  // .category__subtitle

  &__subtitle {
    font-weight: 700;
    font-size: 16px;
    line-height: 169%;
    color: var(--color-base);
  }

  // .category__bottom

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }

  // .category__dots

  &__dots {
    position: relative;
    cursor: pointer;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    width: 24px;
    height: 24px;
    .dots {
      pointer-events: none;
      margin: 0px 0px 5px 0px;
      svg {
        fill: #c8c8c8;
      }
    }
  }

  // .category__text

  &__text {
    font-weight: 700;
    font-size: 12px;
    line-height: 133%;
    color: var(--color-base);
    span {
      font-weight: 400;
      color: #c8c8c8;
    }
  }

  // .category__dots

  &__dots {
    cursor: pointer;
    color: #c8c8c8;
    transition: opacity 0.3s;
    svg {
      fill: currentColor;
    }
    &:hover {
      opacity: 0.8;
    }
  }

  // .category__arrow-prev

  &__arrow-prev {
    left: -36px;
  }

  // .category__arrow-next

  &__arrow-next {
    right: -36px;
  }
}
.navigation-category {
  position: relative;
  display: flex;
  // .navigation-category__slider

  &__slider {
    position: relative;
    margin: 0px 20px 0px 0px;
    @media (max-width: 1600px) {
      margin: 0px 20px 0px 10px;
    }
    @media (max-width: 992px) {
      margin: 0px -20px;
      width: calc(100% + 40px);
      padding: 0px 20px;
    }
    &::before {
      content: "";
      position: absolute;
      right: -48px;
      top: 0;
      width: 136px;
      height: 49px;
      z-index: 5;
      background: linear-gradient(90deg, rgba(245, 242, 236, 0) 0%, #f5f2ec 100%);
      @media (max-width: 992px) {
        display: none;
      }
    }
    &._end {
      &::before {
        display: none;
      }
    }
  }

  // .navigation-category__wrapper

  &__wrapper {
  }

  // .navigation-category__slide

  &__slide {
    width: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    height: auto;
    border-radius: 8px;
    padding: 8px 20px;
    font-weight: 400;
    font-size: 16px;
    line-height: 131%;
    white-space: nowrap;
    transition: border 0.3s;
    border-radius: 50px;
    border: 1px solid transparent;
    @media (max-width: 768px) {
      font-size: 13px;
      line-height: 162%;
      padding: 5px 15px;
    }
    &._active {
      border: 1px solid var(--color-black);
      color: var(--color-red);
    }
    &:hover {
      border: 1px solid var(--color-black);
    }
  }

  // .navigation-category__arrow-prev

  &__arrow-prev {
    color: var(--color-grey);
    left: -20px;
    @media (max-width: 1600px) {
      left: -10px;
    }
    @media (max-width: 992px) {
      display: none;
    }
  }

  // .navigation-category__arrow-next

  &__arrow-next {
    color: var(--color-grey);
    right: 0;
    @media (max-width: 992px) {
      display: none;
    }
  }
}

//========================================================================================================================================================

.share-thoughts {
  padding: 0px 0px 70px 0px;
  @media (max-width: 768px) {
    display: none;
  }

  .container {
    max-width: 800px;
  }

  // .share-thoughts__content

  &__content {
    border: 1px solid var(--color-black);
    border-radius: 8px;
    padding: 10px 20px;
    display: flex;
    align-items: center;
    span {
      font-weight: 400;
      font-size: 17px;
      line-height: 124%;
      color: var(--color-grey);
    }
  }

  // .share-thoughts__image

  &__image {
    flex: 0 0 40px;
    margin: 0px 10px 0px 0px;
    img {
      width: 40px;
      height: 40px;
      border-radius: 50%;
    }
  }
}

//========================================================================================================================================================

.posts {
  padding: 0px 0px 53px 0px;
  @media (max-width: 768px) {
    padding: 0px 0px 20px 0px;
  }

  .container {
    max-width: 800px;
    @media (max-width: 992px) {
      max-width: 100%;
    }
  }

  // .posts__content

  &__content {
    display: flex;
    flex-direction: column;
    gap: 20px 0px;
  }

  // .posts__post

  &__post {
  }
}

.post {
  &._hide {
    display: none;
  }
  // .post__avatar

  &__avatar {
    flex: 0 0 40px;
    margin: 0px 10px 0px 0px;
    img {
      width: 40px;
      height: 40px;
      border-radius: 50%;
    }
  }

  // .post__content

  &__content {
    margin: 0px 0px 0px 50px;
    border-bottom: 1px solid var(--color-grey);
    padding: 0px 0px 20px 0px;
    @media (max-width: 992px) {
      margin: 0px 0px 0px 0px;
    }
    p {
      font-weight: 400;
      font-size: 14px;
      line-height: 143%;
      margin: 0px 0px 20px 0px;
      @media (max-width: 992px) {
        br {
          display: none;
        }
      }
    }
  }

  // .post__header

  &__header {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    @media (max-width: 992px) {
      margin: 0px 0px 10px 0px;
    }
  }

  // .post__left

  &__left {
    display: flex;
    align-items: center;
    svg {
      color: #000;
      margin: 0px 0px 0px 10px;
    }
  }

  // .post__titles

  &__titles {
    display: flex;
    align-items: center;
  }

  // .post__name

  &__name {
    font-weight: 700;
    font-size: 16px;
    line-height: 169%;
  }

  // .post__time

  &__time {
    font-weight: 400;
    font-size: 12px;
    line-height: 133%;
    color: var(--color-grey);
    margin: 0px 0px 0px 10px;
  }

  // .post__line

  &__line {
    position: absolute;
    top: 0;
    left: -28px;
    width: 2px;
    height: 100%;
    background: #d9d6d0;
    @media (max-width: 992px) {
      display: none;
    }
  }

  // .post__comment-icon

  &__comment-icon {
    display: flex;
    align-items: center;
    gap: 20px;
  }

  // .post__icon

  &__icon {
    width: 20px;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    svg {
      transition: fill 0.3s;
      * {
        fill: var(--color-grey);
      }
    }
    &:hover {
      svg {
        * {
          fill: var(--color-black);
        }
      }
    }
  }

  // .post__right

  &__right {
    display: flex;
    align-items: center;
  }

  // .post__subscribe

  &__subscribe {
    font-weight: 700;
    font-size: 13px;
    color: var(--color-red);
    transition: color 0.3s;
    &._active {
      color: var(--color-grey);
    }
    &:hover {
      color: var(--color-black);
    }
  }

  // .post__sliders

  &__sliders {
    position: relative;
    margin: 0px 0px 20px 0px;
  }

  // .post__slider

  &__slider {
    @media (max-width: 992px) {
      margin: 0px -20px;
      padding: 0px 20px;
      width: calc(100% + 40px);
    }
  }

  // .post__wrapper

  &__wrapper {
  }

  // .post__slide

  &__slide {
    display: flex;
    height: auto;
    position: relative;
    overflow: hidden;
    border-radius: 8px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    picture {
      width: 100%;
    }
    &:hover {
      .post__slide-bg {
        opacity: 1;
      }
    }
  }

  // .post__slide-bg

  &__slide-bg {
    transition: opacity 0.5s;
    opacity: 0;
    cursor: pointer;
    background: rgba(0, 0, 0, 0.4);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    color: var(--color-base);
    display: flex;
    justify-content: center;
    align-items: center;
    &::before {
      content: "";
      position: absolute;
      top: 11px;
      left: 12px;
      width: calc(100% - 24px);
      height: calc(100% - 22px);
      border: 1px solid #fff;
      border-radius: 10px;
    }
  }

  // .post__footers

  &__footers {
    display: flex;
    align-items: center;
    gap: 20px;
    justify-content: space-between;
  }

  // .post__data

  &__data {
    font-weight: 400;
    font-size: 12px;
    color: var(--color-grey);
  }

  // .post__footer

  &__footer {
    display: flex;
    align-items: center;
    gap: 50px;
    font-weight: 400;
    font-size: 12px;
    line-height: 133%;
    color: var(--color-grey);
    span {
      display: block;
      margin: 0px 0px 0px 5px;
    }
    .post-icon {
      cursor: pointer;
      display: flex;
      align-items: center;
      transition: color 0.3s;
      svg {
        fill: currentColor;
      }
      &:hover {
        color: var(--color-red);
      }
    }
  }

  // .post__like

  &__like {
    position: relative;
    &._active {
      .post__like-icon {
        opacity: 0;
      }
      .post__like-active {
        animation: fadeOut 0.6s forwards;
      }
      .post__like-active2 {
        opacity: 1;
      }
      span {
        color: var(--color-red);
      }
    }
  }

  // .post__like-icon

  &__like-icon {
  }

  // .post__like-active

  &__like-active {
    position: absolute;
    top: -6px;
    left: -5px;
    opacity: 0;
    transition: opacity 0.3s;
  }

  // .post__like-active2

  &__like-active2 {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    transition: opacity 0.3s ease 0.3s;
  }

  // .post__comment

  &__comment {
  }

  // .post__reset

  &__reset {
  }

  // .post__share

  &__share {
  }
  // .post__arrow-prev

  &__arrow-prev {
    left: -45px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--color-grey);
    color: var(--color-base);
    &:hover {
      color: var(--color-base);
      background-color: var(--color-red);
    }
    @media (max-width: 992px) {
      left: 20px;
      width: 20px;
      height: 20px;
      background-color: rgb(245, 242, 236, 0.7);
      color: rgb(5, 5, 5, 0.7);
      svg {
        width: 10px;
        height: 8px;
      }
    }
  }

  // .post__arrow-next

  &__arrow-next {
    right: -45px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--color-grey);
    color: var(--color-base);
    &:hover {
      color: var(--color-base);
      background-color: var(--color-red);
    }
    @media (max-width: 992px) {
      right: 20px;
      width: 20px;
      height: 20px;
      background-color: rgb(245, 242, 236, 0.7);
      color: rgb(5, 5, 5, 0.7);
      svg {
        width: 10px;
        height: 8px;
      }
    }
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
